import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { EcommerceComponent } from './ecommerce.component';
import { RouterModule, Routes } from '@angular/router';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { FormsModule } from '@angular/forms';


const routes: Routes = [
  {
    path: '',
    component: EcommerceComponent
  },
  {
    path: 'shopee',
    loadChildren: () => import('./shopee/shopee.module').then(m => m.ShopeeModule)
  },
  {
    path: 'sendo',
    loadChildren: () => import('./sendo/sendo.module').then(m => m.SendoModule)
  },
  {
    path: 'lazada',
    loadChildren: () => import('./lazada/lazada.module').then(m => m.LazadaModule)
  },
  {
    path: 'tiki',
    loadChildren: () => import('./tiki/tiki.module').then(m => m.TikiModule)
  },
  {
    path: 'tiktok',
    loadChildren: () => import('./tiktok/tiktok.module').then(m => m.TiktokModule)
  },
  {
    path: 'settings',
    loadChildren: () => import('./settings/settings.module').then(m => m.SettingsModule)
  },
  {
    path: 'analytics',
    loadChildren: () => import('./analytics/analytics.module').then(m => m.AnalyticsModule)
  }
]

@NgModule({
  declarations: [EcommerceComponent],
  imports: [
    CommonModule,
    RouterModule.forChild(routes),
    NgZorroAntModule,
    TranslateModule,
    IonicModule,
    FormsModule
  ]
})
export class EcommerceModule { }
